.position{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
}


.imgwrap{
border: 1px solid lightgray;
border-radius: 50px;
overflow: hidden;
display: flex;
align-items: center;
justify-content: center;
height: 300px;


}



.imgwrap img{
  width: 140%;
  
}

.imgwrap:hover img{
  width: 110%;
}



.Headerstyle{
height: 100vh;
color: white;
overflow: hidden;
position: relative;
display: flex;
align-items: center;
justify-content: center;
background-color: rgba(0, 0, 0, 0.913);


}

.cover{
  height: 100vh;
  width: 80%;
  background-image: url(./PSD_10.jpg);
  filter: opacity(0.5);
background-size: cover;
background-position: center;
object-fit: cover;;
position: absolute;
position: relative;
}





.bg{
  height: 100vh;
  background-size: cover;
  background-image: url(./bg.png);
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: -80px;

}


.project{
  background: rgb(255, 255, 255);
  width: 100%;
  margin-top: 100px;
  /* margin: 50px; */
  border-radius: 50px;
  z-index: 50;;
  height: inherit;

  /* top: -30px; */
